import {action, observable} from "mobx";
import {getAsync} from "../../../common/fetch/getAsync";
import {closeLastModal} from "../../../common/component/modal/modalStore";
import {successToast} from "../../../common/toast/success";
import {postAsync} from "../../../common/fetch/postAsync";

const settingsState = observable({
    email: ""
})

const getSettingsAsync = action(async () => {
    await getAsync("/User/GetSettings").then(response => {
        settingsState.email = response.data.email
    })

    return null;
})

const addChangePasswordRequestAsync = action(async (form: any) => {
    const md5 = require('md5');

    const afterPost = () => {
        closeLastModal()
        successToast("Запрос на смену пароля успешно создан. Проверьте электронную почту, на нее отправлено подтверждение.");
    }

    await postAsync<Object>("User/AddChangePasswordRequest", {
        password: md5(form.password)
    }, afterPost)
})

const addChangeEmailRequestAsync = action(async (form: any) => {
    const afterPost = () => {
        closeLastModal()
        successToast("Запрос на смену электронной почты успешно создан. Проверьте электронную почту, на нее отправлено подтверждение.");
    }

    await postAsync<Object>("User/AddChangeEmailRequest", {
        email: form.email
    }, afterPost)
})

export {settingsState, getSettingsAsync, addChangePasswordRequestAsync, addChangeEmailRequestAsync}