import {RouteObject} from "react-router-dom";
import {Index} from "../../../page/user/index/Index";
import {Index as TicketListIndex} from "../../../page/user/ticket/ticket-list/Index";
import {Index as TicketView} from "../../../page/user/ticket/ticket-view/Index";
import {Index as ReferralIndex} from "../../../page/user/referral-program/Index"
import {Index as SettingsIndex} from "../../../page/user/settings/Index"
import {PrivateRoute} from "../component/PrivateRoute";
import {getReferralPageAsync} from "../../../page/user/referral-program/store";
import {getUserTickets} from "../../../page/user/ticket/ticket-list/store";
import {getTicketView} from "../../../page/user/ticket/ticket-view/store";
import {getSettingsAsync} from "../../../page/user/settings/store";

export const userRoute : RouteObject[] = [
    {
        path: "/",
        element: <PrivateRoute chiledern={<Index/>}/>
    },
    {
        path: "/tickets",
        element: <PrivateRoute chiledern={<TicketListIndex/>}/>,
        loader: async () => await getUserTickets()
    },
    {
        path: "/ticket/:ticketId",
        element: <PrivateRoute chiledern={<TicketView/>}/>,
        loader: async ({params}) => await getTicketView(params.ticketId!)
    },
    {
        path: "/referral",
        element: <PrivateRoute chiledern={<ReferralIndex/>} />,
        loader: async () => await getReferralPageAsync()
    },
    {
        path: "/settings",
        element: <PrivateRoute chiledern={<SettingsIndex/>} />,
        loader: async () => await getSettingsAsync()
    }
]
